import './styles/roleplaySpeech.css';
import { useEffect, useState, useRef } from 'react';
import RecordRTC from 'recordrtc'; 
import { MicVAD, DEFAULT_MODEL } from '@ricky0123/vad-web'
import { sendAudioPost } from './utils/api';


const RoleplaySpeech = ({ 
    callingCoversationDetails, 
    roleplayDetailedInformation,
    closeSpeech
}) => {
    // 状态管理
    const [isInCall, setIsInCall] = useState(false);
    const [vadLoaded, setVadLoaded] = useState(false);
    const [isMuted, setIsMuted] = useState(false);
    const [isSpeaking, setIsSpeaking] = useState(false);
    const [isPlayingServerAudio, setIsPlayingServerAudio] = useState(false);
    const [isSending, setIsSending] = useState(false);

    // 引用管理
    const vadRef = useRef(null);
    const mediaStreamRef = useRef(null);
    const recorderRef = useRef(null);
    const serverAudioRef = useRef(null);
    const audioContextRef = useRef(null);
    const intervalRef = useRef(null); // 用于控制音频片段发送间隔

    // 1. 加载VAD模型（页面初始化时预加载）
    useEffect(() => {
        const loadVAD = async () => {
            try {
                vadRef.current = await MicVAD.new({
                    model: DEFAULT_MODEL,
                    onFrameProcessed: (frame) => {
                        // 处理每帧的检测结果
                        setIsSpeaking(frame.speechProbability > 0.7);
                    },
                    bufferLength: 1024, // 音频缓冲区长度
                });
                setVadLoaded(true);
            } catch (err) {
                alert('VAD模型加载失败:' + err.message);
                setVadLoaded(false);
            }
        };
        loadVAD();

        // 组件卸载清理
        return () => {
            endCall();
        };
  }, []);

    // 2. 启动麦克风与VAD监听（用户触发，需授权麦克风）
    const stratCall = async () => {
        if (!vadLoaded) {
            alert('VAD模型未加载完成, 请稍等');
            return;
        };

        try {
            // 申请麦克风权限，获取16kHz采样率音频流（适配语音模型）
            const stream = await navigator.mediaDevices.getUserMedia({
                audio: { 
                    sampleRate: 16000, 
                    echoCancellation: true ,
                    noiseSuppression: true,
                } // 开启回声消除优化
            });
            mediaStreamRef.current = stream;

            // 初始化音频上下文（用于播放服务器返回的音频）
            audioContextRef.current = new (window.AudioContext || window.webkitAudioContext)();

            // 启动 VAD 检测（新版本启动方法）
            await vadRef.current.start();

            // 初始化录音器（用于录制短音频片段）
            recorderRef.current = RecordRTC(stream, {
                type: 'audio',
                mimeType: 'audio/wav',
                sampleRate: 16000,
                numberOfAudioChannels: 1,
                timeSlice: 1000, // 每1秒生成一个音频片段
                ondataavailable: handleAudioChunk
            });   

            // 开始录音（持续生成音频片段）
            recorderRef.current.startRecording(); 
            // 设置通话状态
            setIsInCall(true);

        } catch (err) {
            alert('请允许麦克风权限，否则无法检测语音');
        }
    };

    // 3. 处理音频片段（每1秒发送一次）
    const handleAudioChunk  = async(blob) => {
        // 过滤条件：仅在通话中、未静音、未在发送中时发送
        if (!isInCall || isMuted || isSending) return;

        setIsSending(true);
        try {
            // 封装音频片段为FormData
            const formData = new FormData();
            formData.append('voiceChunk', blob, `chunk-${Date.now()}.wav`);

            // 调用后端POST接口发送音频片段
            const url = `${process.env.REACT_APP_API_BASE_URL}/api/conversations/${callingCoversationDetails.conversationId}/audio-stream`;
            const response = await sendAudioPost(url, formData);

            // 如果后端返回音频响应，播放它
            if (response?.audioUrl) {
                playServerAudio(response.audioUrl);
            }
        } catch (err) {
            alert('音频片段发送失败:' + err.message);
        } finally {
            setIsSending(false);
        }
  };

    // 4. 播放服务器返回的音频
    const playServerAudio = async (audioUrl) => {
        if (!audioContextRef.current) return;

        try {
            setIsPlayingServerAudio(true);
            const audioElement = serverAudioRef.current;
            audioElement.src = audioUrl;
            await audioElement.play();
        } catch (err) {
            alert('播放服务器音频失败:' + err.message);
            setIsPlayingServerAudio(false);
        }
    };

      // 5. 结束通话
    const endCall = () => {
        if (!isInCall) return;

        // 停止录音
        if (recorderRef.current) {
            recorderRef.current.stopRecording();
            recorderRef.current.destroy();
        }

        // 停止 VAD（新版本停止方法）
        if (vadRef.current) {
            vadRef.current.stop();
        }

        // 停止媒体流
        if (mediaStreamRef.current) {
            mediaStreamRef.current.getTracks().forEach(track => track.stop());
        }

        // 关闭音频上下文
        if (audioContextRef.current) {
            audioContextRef.current.close();
        }


        // 重置状态
        setIsInCall(false);
        setIsSpeaking(false);
        setIsPlayingServerAudio(false);
        setIsMuted(false);
        setIsSending(false);

        // 通知父组件
        closeSpeech();
    };

    // 6. 切换静音
    const toggleMute = () => {
        setIsMuted(!isMuted);
        // 直接禁用麦克风轨道（比停止录音更高效）
        if (mediaStreamRef.current) {
                mediaStreamRef.current.getAudioTracks().forEach(track => {
                track.enabled = !isMuted;
            });
        }
    };


    return (
        <div className='roleplaySpeech'>
            <div className="roleplaySpeech-container">
                <div className="roleplaySpeech-img-container">
                    {/* 角色图片 */}
                    <img 
                        src={roleplayDetailedInformation.cover} 
                        className="roleplaySpeech-img" 
                        alt="角色封面"
                    />
                    
                    {/* 播放动画 (服务器在说话) */}
                    <div className="ripple-container">
                        <div className={`ripple ${isPlayingServerAudio ? 'playing' : ''}`}></div>
                        <div className={`ripple ${isPlayingServerAudio ? 'playing' : ''}`}></div>
                        <div className={`ripple ${isPlayingServerAudio ? 'playing' : ''}`}></div>
                        <div className={`ripple ${isPlayingServerAudio ? 'playing' : ''}`}></div>
                    </div>
                </div>
                
                <div className="roleplaySpeech-content">
                    {/* 语音波浪 (用户在说话/录音) */}
                    <div className={`voice-wave ${isSpeaking && !isMuted ? 'active' : ''}`}>
                        {/* 这些 bar 通常通过 CSS 动画模拟音量波动 */}
                        <span className="bar" />
                        <span className="bar" />
                        <span className="bar" />
                        <span className="bar" />
                        <span className="bar" />
                    </div>                 
                </div>

                {/* 通话控制按钮 */}
                <div className="call-controls">
                {!isInCall ? (
                    <button 
                    className="start-call-btn" 
                    onClick={stratCall}
                    disabled={!vadLoaded}
                    >
                    开始通话
                    </button>
                ) : (
                    <>
                    <button 
                        className={`control-btn mute-btn ${isMuted ? 'active' : ''}`}
                        onClick={toggleMute}
                    >
                        {isMuted ? '取消静音' : '静音'}
                    </button>
                    <button 
                        className="control-btn end-call-btn" 
                        onClick={endCall}
                    >
                        结束通话
                    </button>
                    </>
                )}
                </div>

                {/* 服务器音频播放元素 */}
                <audio 
                ref={serverAudioRef} 
                onEnded={() => setIsPlayingServerAudio(false)}
                style={{ display: 'none' }}
                />
            </div>
        </div>
    );
}

export default RoleplaySpeech;